import React, { useContext } from 'react';
import { connect } from "react-redux";

const Products = ({product, addCurrentView}) => {


  // let findExistedMovie =  carts.find(o => o.id === product.id);
  // let disableButton = findExistedMovie ? true:false;

  return (
    <div className="product" onclick={() => addCurrentView(product.id)}>
      <div className="product_box1">
        <div className="product_img">
          <img src={product.img} alt="images"/>
        </div>
        <div className="product_text">
          <p>{product.name}</p>
          <p>UGX {product.price}</p>
        </div>
      </div>
    </div>
  )
}

const mapDispatchToProps = (dispatch) => {
  // addCurrentView : (dispatch)=> {
  //   addCurrentView()
  // }
}

export default connect(null, mapDispatchToProps)(Products);
